.hero {
  background-color: $primary-bg-color;
}

.hero-container {
  position: relative;

  @include mq(mobile-only) {
    height: 100vh;
    margin: 0;
    padding: 100px 20px 0px 20px;
  }
}

h1 {
  font-family: 'Seymour One';
  font-weight: 400;
  font-size: 26px;
  line-height: 1.25;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  padding: 30px 0px 30px 0px;
}

h1 > span {
  font-size: 34px;
}

.hero-content__button {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 20px;
}

.hero-content__button li:not(:last-child) {
  margin-right: 12px;
}

.establishments {
  display: none;
}

.offer {
  display: none;
}

.offer__btn {
  display: none;
}

.hero-img2 {
  z-index: 1;
  position: relative;
  bottom: -40%;
  left: -20px;
}

.hero-img2 img {
  max-width: none;
}

.hero-img3 {
  position: relative;
  z-index: 2;
  bottom: 6%;
  left: 25%;
}
